// hooks/useProductosConPromo.ts
import { PromoType } from "@/lib/localPromo";
import { useEffect, useState } from "react";

export interface Producto {
    id: number;
    nombre: string;
    precio: number;
    imagen: string;
    etiquetas: string[];
    descripcion: string;
    opciones?: {
        color?: string[];
        sabor?: string[];
        tamaño?: string[];
    };
    promo?: PromoType | undefined; // PromoType | undefined
}

// Entidad promo real
export interface Promo {
    id: number;
    tipo: "descuento" | "2x1" | "nuevo" | "solo-hoy";
    valor?: number;
    nombre: string;
    descripcion: string;
    diasSemana: number[];
}

// Relacion Promo-Producto
interface PromoProductoRel {
    idProducto: number;
    idPromo: number;
}

function UseProductosPromo() {
    const [productos, setProductos] = useState<Producto[]>([]);

    useEffect(() => {
        // Lee productos, promos y relación de localStorage
        const rawProductos = JSON.parse(
            localStorage.getItem("kwikemart_productos") || "[]"
        );
        const promos = JSON.parse(
            localStorage.getItem("kwikemart_promos") || "[]"
        );
        const rel = JSON.parse(
            localStorage.getItem("kwikemart_promo_productos") || "[]"
        );

        // Para cada producto, asocia su promo correspondiente (si tiene)
        const productosConPromo = rawProductos.map((prod: Producto) => {
            // Encuentra la promo relacionada (puede ser varias, pero usamos la primera que aplique)
            const relPromo = (rel as PromoProductoRel[]).find(
                (rp) => rp.idProducto === prod.id
            );
            const promo = relPromo
                ? (promos as Promo[]).find(
                      (p: Promo) => p.id === relPromo.idPromo
                  )
                : undefined;
            return { ...prod, promo };
        });

        setProductos(productosConPromo);
    }, []);

    return productos;
}

export default UseProductosPromo;
